import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-working-component',
  templateUrl: './working-component.component.html',
  styleUrls: ['./working-component.component.css'],
  providers:[Title]
})
export class WorkingComponentComponent implements OnInit {

  constructor(
    private title:Title
  ) {
     this.title.setTitle("ประสบการทำงาน");
   }

  ngOnInit() {
  }

}



