import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { RouterModule, Routes } from '@angular/router';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { HomeComponentComponent } from './home-component/home-component.component';
import { IntroduceComponentComponent } from './introduce-component/introduce-component.component';
import { WorkingComponentComponent } from './working-component/working-component.component';
import { EducationComponentComponent } from './education-component/education-component.component';
import { ContractComponentComponent } from './contract-component/contract-component.component';


const appRouter : Routes = [
  {path:"",component:HomeComponentComponent},
  {path:"home",component:HomeComponentComponent},
  {path:"introduce",component:IntroduceComponentComponent},
  {path:"work",component:WorkingComponentComponent},
  {path:"education",component:EducationComponentComponent},
  {path:"contract",component:ContractComponentComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    HomeComponentComponent,
    IntroduceComponentComponent,
    WorkingComponentComponent,
    EducationComponentComponent,
    ContractComponentComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRouter,{ useHash: true }),
    NgbModule.forRoot()
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



